.modal {
    position: fixed;
    z-index: 1;
    padding-top: 80px;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0,0,0,0.8);
}

.modal-content {
    border-radius: 5px;
    background-color: rgba(30,32,35, 1);;
    margin: auto;
    padding: 20px;
    width: 60%;
    max-height: calc(100vh - 210px);
    overflow-y: auto;
}

.modal-form {
    border: 2px solid rgba(255, 255, 255, .1);
    padding: 20px;
    width: 80%;
    margin: auto;
}

.modal-form label {
    float: left;
}

.modal-form input[type=text], select {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid rgb(13,17,23);
    border-radius: 4px;
    box-sizing: border-box;
    background-color: rgb(67,66,66);
    color: #fff;
    font-size: 16px;
}

.modal-form textarea {
    margin: 8px 0;
    width: 100%;
    padding: 12px 20px;
    height: 150px;
    box-sizing: border-box;
    border: 1px solid rgb(13,17,23);
    border-radius: 4px;
    background-color: rgb(67,66,66);
    font-size: 16px;
    resize: none;
    color: #fff;
}

.close {
    color: #aaaaaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}

.close:hover,
.close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
}

.actions {
    margin: 10px;
    padding-left: 50px;
    float: right;
}

.actions button {
    padding: 10px 30px;
    border-radius: 5px;
    border: none;
    color: #fff;
    margin: 10px;
}

.actions .save-button {
    background-color: rgba(0, 163, 49, 1);
}

.actions .cancel-button {
    background-color: rgba(247, 54, 54, 1);
}

.actions .transparent-button {
    background-color: transparent;
}

ul.history {
    list-style-type: none;
    float: left;
}

ul.history li {
    margin-top: 10px;
    text-align: left;
}

ul.history li small {
    color: #7b7b7b;
}

.table-list {
    margin-top: 30px;
    margin-bottom: 30px;
}

nav{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 0 auto;
    padding: 20px;
    background-color: #d0bebe17;

}
nav h1{
    text-transform: uppercase;
}
nav ul li{
    display: inline-block;
    list-style: none;
    margin: 0 10px;
}
nav ul li button{
    background: none;
    border: none;
    padding: 0;
    cursor: pointer;
    font-size: 1em;
    font-weight: bold;
    text-decoration: none;
    color: rgba(255, 255, 255, .5);
    position: relative;
    display: inline-block;
    outline:none;
}
nav ul li button::before{
    content: attr(data-text);
    position: absolute;
    left: 0;
    top: 0;
    width: 0;
    white-space: nowrap;
    overflow: hidden;
    transition: .5s;
    border-bottom: 3px solid orange;

}
nav ul li button:hover::before{
    width: 100%;
}

@media(max-width:700px){
    nav{
        display: block;
        text-align: center;
    }

    nav ul li{
        display: block;
        padding: 10px 0;
        text-align: left;
    }
}

.google-btn {
    width: 184px;
    height: 42px;
    background-color: #4285f4;
    border-radius: 2px;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, .25);
    margin: auto;
    cursor: pointer;
}
.google-btn .google-icon-wrapper {
    position: absolute;
    margin-top: 1px;
    margin-left: 1px;
    width: 40px;
    height: 40px;
    border-radius: 2px;
    background-color: #fff;
}
.google-btn .google-icon {
    position: absolute;
    margin-top: 11px;
    margin-left: 11px;
    width: 18px;
    height: 18px;
}
.google-btn .btn-text {
    float: right;
    margin: 11px 11px 0 0;
    color: #fff;
    font-size: 14px;
    letter-spacing: 0.2px;
    font-family: "Roboto", serif;
}
.google-btn:hover {
    box-shadow: 0 0 6px #4285f4;
}
.google-btn:active {
    background: #1669f2;
}


